<template>
  <header>
    <div class="content">
      <div class="logo">
        袋鼠互动
      </div>
      <div class="uinfo">
        <div class="uname">
          <i class="fa fa-user"></i> {{ getUsrName }}
        </div>
        <div class="logout" @click="logout">
          <i class="fa fa-sign-out"></i> 退出
        </div>
      </div>
    </div>
  </header>  
</template>

<script>
import post from '../../assets/js/utils';
import { mapGetters, mapMutations } from "vuex";

export default {
  methods: {
    ...mapMutations({
      setUsrName: 'setUsrName',
    }),
    async logout() {
      const {success, msg, data} = await post('/users/logout');

      if (success) {
        this.$router.replace('/login');
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    }
  },
  async mounted() {
    // const { data: { success, msg, data } } = await this.$http.get('/users');

    // if (success) {
    //   this.setUsrName(data.name);
    // }
  },
  computed: {
    ...mapGetters(["getUsrName"])
  }
};
</script>

<style lang="scss" scoped>
  @import '../../assets/scss/var.scss';

  header {
    width: 100%;
    height: 60px;
    background-color: $base-color;
    color: $white;
    line-height: 60px;

    .content {
      width: 1300px;
      height: 60px;
      margin: 0 auto;

      display: flex;
      
      .logo {
        flex: 4;
      }

      .uinfo {
        flex: 8;
        display: flex;
        justify-content: flex-end;

        .uname {
          margin-right: 20px;  
          height: 60px;
        }

        .logout {
          padding: 0 10px;
          
          &:hover {
            background: $deep-base-color;
            cursor: pointer;
          }
        }
      }
    }
  }

  @media (max-width: 1300px) {
    header {
      width: 1300px;
    }
  }
</style>



