import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/views/login/';
import BackStage from '@/views/backstage/';
import UserMgr from '@/views/usermgr/';
import ActMgr from '@/views/actmgr/';
import UserDetail from '@/views/userdetail/';
import ActDetail from '@/views/actdetail/';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/backstage',
      name: 'backstage',
      component: BackStage,
      redirect: '/backstage/actmgr',
      children: [
        {
          path: 'usermgr',
          component: UserMgr,
        },
        {
          path: 'actmgr',
          component: ActMgr
        },
        {
          path: 'usermgr/:id',
          component: UserDetail
        },
        {
          path: 'actmgr/:id',
          component: ActDetail
        }
      ]
    }
  ]
})



