<template>
  <div id="login">
    
    <div id="login-form">
      <div class="title">袋鼠后台管理系统</div>

      <div class="tips" v-if="showTips">
        {{ tips }}
      </div>

      <div class="item">
        <el-input type="text" placeholder="用户名" v-model="uname"></el-input>
      </div>

      <div class="item">
        <el-input type="password" placeholder="密码" v-model="pwd"></el-input>
      </div>

      <div class="item">
        <el-input type="text" placeholder="验证码" v-model="code" style="width: 60%; float: left;"></el-input>
        <div style="float: left; width: 40%;height: 40px;">
          <img :src="codesrc" alt="" @click="refreshcode" style="width: 100%; height: 100%">
        </div>
      </div>

      <div style="clear: both;"> </div>

      <div class="item" style="margin-top:30px;">
        <el-button type="primary" @click="login">登陆</el-button>
      </div>

    </div>

  </div>
</template>

<script>
import post from '../../assets/js/utils';
import { mapMutations } from 'vuex';

export default {
  methods: {
    ...mapMutations({
      setUsrName: 'setUsrName',
    }),
    async login() {
      if (this.uname === '' || this.pwd === '' || this.code === '') {
        this.showTips = true;
        this.tips = '用户名、密码或验证码不能为空';
        return;
      }
      const { data: { data, success, msg } } = await this.$http.get('/admins/login', {
        params: {
          user: this.uname,
          password: this.pwd,
          verifiCode: this.code
        }
      });

      if (success) {
        this.$router.push('/backstage');
        this.setUsrName(this.uname);
        return;
      }

      this.showTips = true;
      this.tips = msg;
      this.refreshcode();
    },

    refreshcode() {
      this.codesrc = this.basecodeurl + '?' + (parseInt(Math.random() * 19999)).toString();
    }
  },

  data() {
    return {
      showTips: false,
      uname: '',
      pwd: '',
      code: '',
      tips: '',
      basecodeurl: '/admins/verification',
      codesrc: '/admins/verification',
    };
  }
}
</script>

<style lang="scss" scoped>
  @import '../../assets/scss/var.scss';

  #login {
    
    #login-form {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -70%);
      width: 450px;
      padding: 20px;
      box-shadow: 0 0 30px #efefef;
      border-top: 4px solid $base-color;

      .tips {
        width: 100%;
        height: 30px;
        border-left: 4px solid $red;
        line-height: 30px;
        padding-left: 10px;
        margin-bottom: 15px;
        color: $red;
      }

      .title {
        font-size: 1.5rem;
        text-align: center;
        margin-bottom: 15px;
        color: $base-color;
      }

      .item {
        margin-bottom: 30px;

        &:last-child {
          margin-bottom: 0;
        }

        span {
          display: block;
          margin-bottom: 10px;
          font-size: 1.1rem;
        }

        button {
          width: 100%;
        }
      }
    }
  }
  @media (max-width: 700px) {
    #login {
      #login-form {
        width: 80%;
      }
    }
  }
</style>



