<template>
  <div id="userdetail">
    <div class="empty"></div>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>个人信息</span>
        <div style="float: right; cursor: pointer;" v-loading="loading">
          <span @click="edit"><div class="fa fa-pencil"></div> 修改</span>
        </div>
      </div>


      <el-dialog
        title="修改"
        :visible.sync="editForm"
        width="1000px">

        <el-form ref="form" :model="editData" label-width="80px">
          <el-form-item label="用户名">
            <el-input v-model="editData.name"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-input v-model="editData.sex"></el-input>
          </el-form-item>
          <el-form-item label="电话">
            <el-input v-model="editData.phone"></el-input>
          </el-form-item>
          <el-form-item label="地区">
            <el-input v-model="editData.area"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="editData.remark"></el-input>
          </el-form-item>
          <el-form-item label="锁定">
            <el-switch
              v-model="editData.locked"
              active-color="#13ce66"
              inactive-color="#ff4949">
            </el-switch>

          </el-form-item>

          
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="editForm = false">取 消</el-button>
          <el-button type="primary" @click="update">确 定</el-button>
        </span>
      </el-dialog>

      <div v-loading="loading">
         <el-form label-position="left" inline class="usrinfo" >
          <el-form-item label="用户名" label-width="90px">
            <span>{{ user.name }}</span>
          </el-form-item>
          <el-form-item label="性别" label-width="90px">
            <span>{{ user.sex }}</span>
          </el-form-item>
          <el-form-item label="电话" label-width="90px">
            <span>{{ user.phone }}</span>
          </el-form-item>
          <el-form-item label="地区" label-width="90px">
            <span>{{ user.area }}</span>
          </el-form-item>
          <el-form-item label="备注" label-width="90px">
            <span>{{ user.remark }}</span>
          </el-form-item>
          <el-form-item label="锁定" label-width="90px">
            <span>{{ user.lockedFlag ? '是' : '否' }}</span>
          </el-form-item>
        </el-form>
        <p></p>
        <el-form >
         <el-form-item label="兴趣标签">
            <br>
            <el-tag
              v-for="item in userTags"
              :key="item.id"
              @close="delUserTag(item)"
              closable>
              {{ item.name }}
            </el-tag>
            <span style="cursor: pointer">
              <el-tag>
                <span @click="addTagForm = true">
                  添加
                </span>
              </el-tag>
            </span>

            <el-dialog
              title="添加标签"
              :visible.sync="addTagForm"
              width="1000px">

              <el-select v-model="addTagId" placeholder="请选择">
                <el-option
                  v-for="item in tags"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>

              <span slot="footer" class="dialog-footer">
                <el-button @click="addTagForm = false">取 消</el-button>
                <el-button type="primary" @click="addTag">确 定</el-button>
              </span>
            </el-dialog>
          </el-form-item>
        </el-form>
        
      </div>

    </el-card>

    <div class="empty"></div>

    <el-row :gutter="10">
      <el-col :span="12">
        <div class="grid-content bg-purple">
          <el-card class="box-card" body-style="padding: 0;">
            <div slot="header" class="clearfix">
              <span>好友列表</span>
            </div>
            
            <div v-loading="loading">
              <el-table
                :data="showFriends"
                border
                style="width: 100%; border: 0 none; height: 568px;;">
                <el-table-column
                  prop="name"
                  label="用户名">
                </el-table-column>
                <el-table-column
                  prop="area"
                  label="年龄">
                </el-table-column>
                <el-table-column
                  label="操作"
                  >
                  <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="showUserChatHistory(scope.row)">聊天记录</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div class="block" style="text-align: center;">
                <el-pagination
                  @current-change="setFriendsPage"
                  :current-page="1"
                  :page-size="20"
                  layout="total, prev, pager, next, jumper"
                  :total="friendsTotal">
                </el-pagination>
              </div>

              <el-dialog
                title="提示"
                :visible.sync="showChatHistory"
                width="60%">

                <el-table
                  :data="chatHistory"
                  border
                  v-loading="chatHistoryLoading"
                  style="width: 100%;">
                  <el-table-column
                    prop="senderName"
                    label="发送者">
                  </el-table-column>
                  <el-table-column
                    label="时间">
                    <template slot-scope="scope">
                      {{ formatTimestemp(scope.row.createtime) }}
                    </template>
                  </el-table-column>
                  <el-table-column
                    prop="content"
                    label="内容">
                  </el-table-column>
                </el-table>
                <div class="block" style="text-align: center;">
                  <el-pagination
                    @current-change="setChatHistoryPage"
                    :current-page="1"
                    :page-size="20"
                    layout="total, prev, pager, next, jumper"
                    :total="chatTotal">
                  </el-pagination>
                </div>
              </el-dialog>

            </div>
          </el-card>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="grid-content bg-purple">
          <el-card class="box-card" body-style="padding: 0;">
            <div slot="header" class="clearfix">
              <span>活动</span>
              <div style="float: right" class="actType" v-loading="loading">
                <span @click="showAct(1)" :class="{active: this.curType === 1}">创建的</span>
                <span @click="showAct(2)" :class="{active: this.curType === 2}">参加的</span>
                <span @click="showAct(3)" :class="{active: this.curType === 3}">约定的</span>
              </div>
            </div>
            <div v-loading="loading">
              <el-table
                :data="showActList"
                border
                style="width: 100%; border: 0 none; height: 568px;;">
                <el-table-column
                  prop="name"
                  label="活动名称">
                </el-table-column>
                <el-table-column
                  prop="typeName"
                  label="类型">
                </el-table-column>
                <el-table-column
                  prop="area"
                  label="地区">
                </el-table-column>
                <el-table-column
                  prop="max"
                  label="上限人数">
                </el-table-column>
                <el-table-column
                  label="操作"
                  >
                  <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="actDetail(scope.row.id)">详情</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div class="block" style="text-align: center;">
                <el-pagination
                  @current-change="setShowActPage"
                  :current-page="1"
                  :page-size="20"
                  layout="total, prev, pager, next, jumper"
                  :total="actTotal">
                </el-pagination>
              </div>
            </div>
          </el-card>
        </div>
      </el-col>
    </el-row>



  </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { userdetail } from '../../common/config/bread';
import post from '../../assets/js/utils';

export default {
  data() {
    return {
      showActList: [],
      curAct: [],
      actTotal: 0,
      curType: 1,

      friendsTotal: 0,

      showFriends: [],

      user: {},
      showChatHistory: false,

      loading: false,

      chatHistory: [],
      chatTotal: 0,
      chatHistoryLoading: false,
      curfid: '',

      userTags: [],

      editForm:false,
      editData: {},

      addTagForm: false,
      tags: [],
      addTagId: 1,
    }
  },

  methods: {
    ...mapMutations({
      setNavActive: 'setNavActive',
      setCurBread: 'setCurBread',
    }),

    setFriendsPage(index) {
      this.showFriends = [];

      const friends = this.user.friends;

      let start = (index - 1) * 10;
      let end = start + 10;

      for (let i = start; i < end; i ++) {
        if (friends[i]) this.showFriends.push(friends[i]);
      }
    },

    showAct(type) {
      if (type === 1)  this.curAct = this.user.myActivities;
      if (type === 2)  this.curAct = this.user.participatingActivities;
      if (type === 3)  this.curAct = this.user.subscribleActivities;

      this.setShowActPage(1);
      this.actTotal = this.curAct.length || 0;
      this.curType = type;
    },
    
    formatTimestemp(s) {
      const date = new Date(s);

      // yyyy-MM-dd HH:mm
      const yyyy = date.getFullYear();
      const MM = date.getMonth() + 1;
      const dd = date.getDate();
      const HH = date.getHours();
      const mm = date.getMinutes();
      return `${yyyy}-${MM}-${dd} ${HH}:${mm}`;
    },

    setShowActPage(index) {
      this.showActList = [];

      const curAct = this.curAct;

      let start = (index - 1) * 20;
      let end = start + 20;
      for (let i = start; i < end; i ++) {
        if (curAct[i]) this.showActList.push(curAct[i]);
      }
    },

    async addTag(id) {
      let str = '';
      let flag = false;
      this.userTags.forEach(item => {
        str += (item.id + ',');
        if (item.id === this.addTagId) {
          flag = true;
          return;
        }
      });

      if (flag) {
        this.$notify.error({
          title: '错误',
          message: '已经选择该标签'
        });
        return;
      }

      str += (this.addTagId + ',');

      const { data, success, msg } = await post(`/activity-type/${this.$route.params.id}/put`, {
        interest: str
      });
      
      if (success) {
        this.$notify({
          title: '成功',
          message: msg,
          type: 'success'
        });
        this.getUserTags();
        return;
      }
      this.$notify.error({
        title: '错误',
        message: msg
      });
    },

    edit() {
      this.editData = {
        id: this.$route.params.id,
        name: this.user.name,
        sex: this.user.sex,
        area: this.user.area,
        phone: this.user.phone,
        remark: this.user.remark,
        locked: this.user.lockedFlag,
      };
      console.log(this.editData);
      this.editForm = true;
    },

    async update() {
      this.editData.locked = this.editData.lockedFlag ? 1 : 0;
      const { data, success, msg } = await post('/admins/put', this.editData);

      if (success) {
        this.user.name = this.editData.name;
        this.user.sex = this.editData.sex;
        this.user.area = this.editData.area;
        this.user.phone = this.editData.phone;
        this.user.remark = this.editData.remark;
        this.user.locked = this.editData.locked;
        this.user.lockedFlag = this.editData.locked === 1 ? true : false;
        this.$notify({
          title: '成功',
          message: msg,
          type: 'success'
        });
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    },

    showUserChatHistory(item) {
      this.curfid = item.id;
      this.getChatHistory();
      this.showChatHistory = true;
    },

    setChatHistoryPage(index) {
      this.getChatHistory(index);
    },

    async getChatHistory(pagenum = 1) {
      this.chatHistoryLoading = true;

      // const { data: { success, data, msg } } = await this.$http.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/chathistory', {
      const { data: { success, data, msg } } = await this.$http.get('/chattingrecord', {
          
        params: {
          pagenum,
          pagesize: 20,
          uid: this.user.id,
          fid: this.curfid,
        }
      });

      if (success) {
        this.chatHistory = data.rows;
        this.chatTotal = data.total;
        this.chatHistoryLoading = false;
        return;
      }
      // this.$notify.error({
      //   title: '错误',
      //   message: msg
      // });
      this.chatHistory = [];
      this.chatTotal = 0;
      this.chatHistoryLoading = false;
    },

    actDetail(id) {
      this.$router.push(`/backstage/actmgr/${id}`)
    },

    async getUserInfo() {
      this.loading = true;
      // const { data: { success, data, msg } } = await this.$http.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/admins/uid');
      const { data: { success, data, msg } } = await this.$http.get(`/admins/${this.$route.params.id}`);

      if (success) {
        this.user = data;
        this.user.lockedFlag = data.locked === 0 ? false : true;
        this.loading = false;
        this.setFriendsPage(1);
        this.friendsTotal = this.user.friends.length;
        return;
      }

      // this.$notify.error({
      //   title: '错误',
      //   message: msg
      // });
      this.loading = false;
      
    },

    async getUserTags() {
      const { data: { success, data, msg } } = await this.$http.get(`/activity-type/${this.$route.params.id}`);
      // const { data: { success, data, msg } } = await this.$http.get(`https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/userTags`);

      if (success) {
        this.userTags = data;
        return;
      }

      // this.$notify.error({
      //   title: '错误',
      //   message: msg
      // });
      
    },

    async delUserTag(item) {
      let str = '';
      this.userTags.forEach(item => {
        str += (item.id + ',')
      });

      str = str.replace(`${item.id},`, '');

      const { data, success, msg } = await post(`/activity-type/${this.$route.params.id}/put`, {
        interest: str
      });

      if (success) {
        this.userTags.forEach((itemer, index) => {
          if (itemer.id === item.id) {
            this.userTags.splice(index, 1);
          }
        });
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    },

    async getAllTags() {
      const { data: { data, msg, success } } = await this.$http.get('/activity-type');
      // const { data: { data, msg, success } } = await this.$http.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getAllTags');

      if (success) {
        this.tags = data;
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    }
  },

  mounted() {
    this.setNavActive('2');
    this.setCurBread(userdetail);

    this.getUserInfo();
    this.getUserTags();
    this.getAllTags();
    this.timer = setInterval(() => {
      if (this.user.myActivities !== []) {
        this.showAct(1);
        clearInterval(this.timer);
      }
    }, 500);
  }
}
</script>

<style lang="scss" scoped>
  @import '../../assets/scss/var.scss';

  #userdetail {

    .el-tag {
      margin: 0 5px 5px 0;
    }

    .usrinfo {
      font-size: 0;

      label {
        width: 90px;
        color: #99a9bf;
      }

      .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
      }
    }

    .actType {

      span {
        cursor: pointer;
      }
      
      .active {
        color: $base-color;
      }
    }
  }
</style>



