<template>
  <section id="usermgr">

    <div class="empty"></div>

    <el-tabs tab-position="left">
      <el-tab-pane label="用户管理">

        <el-card class="box-card" body-style="padding: 0;">
          <div slot="header" class="clearfix">
            <span>用户列表</span>
          </div>
          
          <div>
            <el-table
              :data="tableData"
              border
              v-loading="loading"
              style="width: 100%; border: 0 none;">
              <el-table-column
                prop="name"
                label="用户名"
                width="180">
              </el-table-column>
              <el-table-column
                prop="area"
                label="地区"
                width="180">
              </el-table-column>
              <el-table-column
                prop="phone"
                label="手机">
              </el-table-column>
              <el-table-column
                prop="sex"
                label="性别">
              </el-table-column>
              <el-table-column
                label="操作"
                >
                <template slot-scope="scope">
                  <el-button type="primary" size="mini" @click="detail(scope.row.id)">详情</el-button>
                  <el-button type="danger" size="mini" @click="del(scope.row.id)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="block" style="text-align: center;">
              <el-pagination
                @current-change="setPage"
                :current-page="1"
                :page-size="20"
                layout="total, prev, pager, next, jumper"
                :total="total">
              </el-pagination>
            </div>
          </div>
        </el-card>


      </el-tab-pane>
      <el-tab-pane label="添加用户">

        <el-form ref="form" :model="addData" label-width="80px">
          <el-form-item label="姓名">
            <el-input v-model="addData.name"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="addData.password"></el-input>
          </el-form-item>
          <el-form-item label="地区">
            <el-input v-model="addData.area"></el-input>
          </el-form-item>
          <el-form-item label="手机">
            <el-input v-model="addData.phone"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="addData.remark"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="addData.sex">
              <el-radio label="男" value="0"></el-radio>
              <el-radio label="女" value="1"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="用户类型">
            <el-radio-group v-model="addData.type">
              <el-radio label="普通用户"></el-radio>
              <el-radio label="管理员"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="是否锁定">
            <el-radio-group v-model="addData.locked">
              <el-radio label="是"></el-radio>
              <el-radio label="否"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addUser">立即创建</el-button>
          </el-form-item>
        </el-form>

      </el-tab-pane>
    </el-tabs>

  </section>
</template>

<script>
import { mapMutations } from 'vuex';
import { usermgr } from '../../common/config/bread';
import post from '../../assets/js/utils';

export default {
  data() {
    return {
      tableData: [],

      total: 0,

      loading: false,

      addData: {
        name: '',
        password: '',
        sex: '男',
        area: '',
        phone: '',
        headpic: 'default0.png',
        locked: '否',
        remark: '',
        type: '普通用户'
      }
    }
  },

  methods: {
    ...mapMutations({
      setNavActive: 'setNavActive',
      setCurBread: 'setCurBread',
    }),
  
    async addUser() {
      let addData = {...this.addData};

      addData.type = addData.type === '普通用户' ? 1 : 0;
      addData.locked = addData.locked === '是' ? 1 : 0;

      const{ data, success, msg } = await post('/admins', addData);

      if (success) {
        this.$notify({
          title: '成功',
          message: msg,
          type: 'success'
        });
        this.addData.name = '';
        this.addData.area = '';
        this.addData.remark = '';
        this.phone = '';
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
      
    },

    async del(id) {

      const { data, success, msg } = await post(`/admins/${id}/del`);

      if (success) {
        this.$notify({
          title: '成功',
          message: msg,
          type: 'success'
        });

        this.tableData.forEach((item, index) => {
          if (item.id === id) {
            this.tableData.splice(index, 1);
          }
        });

        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    },
    
    async getAllUser(pagenum = 1) {
      this.loading = true;
      // const { data: { success, data, msg } } = await this.$http.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getAllUser', {
      const { data: { success, data, msg } } = await this.$http.get('/admins', {
        params: {
          pagenum,
          pagesize: 20,
        }
      });

      if (success) {
        this.tableData = data.rows;
        this.total = data.total;
        this.loading = false;
        return;
      }

      // this.$notify.error({
      //   title: '错误',
      //   message: msg
      // });
      this.loading = false;
    },

    setPage(index) {
      this.getAllUser(index);
    },

    detail(id) {
      this.$router.push(`/backstage/usermgr/${id}`)
    }
  },

  mounted() {
    this.setNavActive('2');
    this.setCurBread(usermgr);

    this.getAllUser();
  }
}
</script>



