<script>
import { mapMutations } from 'vuex';
import { actdetail } from '../../common/config/bread';
import post from '../../assets/js/utils';

export default {
  data() {
    return {
      showdm: false,

      showVote: [],
      showRP: [],
      showSigns: [],
      showSubs: [],
      showLots: [],
      lotsTotal: 0,
      rpTotal: 0,
      voteTotal: 0,
      signsTotal: 0,
      subsTotal: 0,

      actInfo: {},
      active: {},
      rpDetail: false,

      currp: {},

      voteDetail: false,
      votePersionDetail: false,
      curvoteRecords: [],
      curvote: {},

      dm: [],
      dmTotal: 0,
      dmloading: false,

      loadingActData: false,
      lotDetail: false,
      curLotDetail: {},

      editForm: false,
      editData: {},

      tags: []
    }
  },
  
  methods: {
    ...mapMutations({
      setNavActive: 'setNavActive',
      setCurBread: 'setCurBread',
    }),

    async update() {
      const { success, msg, data } = await post('/activity/put', this.editData);

      if (success) {
        this.actInfo.activityDO.name = this.editData.name;
        this.actInfo.activityDO.intro = this.editData.intro;
        this.actInfo.activityDO.area = this.editData.area;
        this.actInfo.activityDO.starttime = this.editData.s;
        this.actInfo.activityDO.endtime = this.editData.e;
        this.actInfo.activityDO.max = this.editData.max;
        this.actInfo.activityDO.remark = this.editData.remark;
        this.actInfo.activityDO.site = this.editData.site;
        this.actInfo.activityDO.typeid = this.editData.typeid;
        
        this.tags.forEach(item => {
          if (item.id === this.editData.typeid) {
            this.actInfo.activityDO.typename = this.editData.typename;
          }
        });

        this.$notify({
          title: '成功',
          message: msg,
          type: 'success'
        });
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    },

    formatTimestemp(s) {
      const date = new Date(s);

      // yyyy-MM-dd HH:mm
      const yyyy = date.getFullYear();
      const MM = date.getMonth() + 1;
      const dd = date.getDate();
      const HH = date.getHours();
      const mm = date.getMinutes();
      return `${yyyy}-${MM}-${dd} ${HH}:${mm}`;
    },

    edit() {
      this.editData = {
        id: this.actInfo.activityDO.id,
        uid: this.actInfo.activityDO.uid,
        name: this.actInfo.activityDO.name,
        intro: this.actInfo.activityDO.intro,
        area: this.actInfo.activityDO.area,
        s: this.formatTimestemp(this.actInfo.activityDO.starttime),
        e: this.formatTimestemp(this.actInfo.activityDO.endtime),
        max: this.actInfo.activityDO.max,
        remark: this.actInfo.activityDO.remark,
        site: this.actInfo.activityDO.site,
        typeid: this.actInfo.activityDO.typeid,
      }
      this.editForm = true;
    },

    showLotDetail(item) {
      this.curLotDetail = item;
      this.lotDetail = true;
    },

    setDMPage(index) {
      this.getdm(index);
    },

    async getdm(pagenum = 1) {
      this.dmloading = true;
      // const { data: { success, data, msg } } = await this.$http.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getdm', {
      const { data: { success, data, msg } } = await this.$http.get(`/chattingrecord/${this.$route.params.id}/barrage`, {
        params: {
          pagenum,
          pagesize: 20
        }
      });

      if (success) {
        this.dm = data.rows;
        this.dmTotal = data.total;
        this.dmloading = false;
        return;
      }

      // this.$notify.error({
      //   title: '错误',
      //   message: msg
      // });
      this.dmloading = false;
    },

    dmShower() {
      if (this.dm.length === 0) {
        this.getdm();
      }
      this.showdm = true;
    },

    // 查看投票
    lookVote(item) {
      this.curvote = item;
      this.voteDetail = true;
    },

    showVotePerson(item) {
      console.log(item);
      this.curvoteRecords = item.voteRecords;
      this.votePersionDetail = true;
    },

    // 查看红包
    lookRp(item) {
      this.currp = item;
      this.rpDetail = true;
    },

    setLotsPage(index) {
      this.showLots = [];

      const lotteries = this.actInfo.lotteries;

      let start = (index - 1) * 10;
      let end = start + 10;

      for (let i = start; i < end; i ++) {
        if (lotteries[i]) this.showLots.push(lotteries[i]);
      }
    },
 
    setSignsPage(index) {
      this.showSigns = [];

      const signs = this.actInfo.signs;

      let start = (index - 1) * 10;
      let end = start + 10;

      for (let i = start; i < end; i ++) {
        if (signs[i]) this.showSigns.push(signs[i]);
      }
    },

    setSubsPage(index) {
      this.showSubs = [];

      const subscrible = this.actInfo.subscrible;

      let start = (index - 1) * 10;
      let end = start + 10;

      for (let i = start; i < end; i ++) {
        if (subscrible[i]) this.showSubs.push(subscrible[i]);
      }
    },

    setRPPage(index) {
      this.showRP = [];

      const redPackets = this.actInfo.redPackets;

      let start = (index - 1) * 10;
      let end = start + 10;

      for (let i = start; i < end; i ++) {
        if (redPackets[i]) this.showRP.push(redPackets[i]);
      }
    },

    formatTimestemp(s) {
      const date = new Date(s);

      // yyyy-MM-dd HH:mm
      const yyyy = date.getFullYear();
      const MM = date.getMonth() + 1;
      const dd = date.getDate();
      const HH = date.getHours();
      const mm = date.getMinutes();
      return `${yyyy}-${MM}-${dd} ${HH}:${mm}`;
    },

    setVotePage(index) {
      this.showVote = [];

      const votes = this.actInfo.votes;

      let start = (index - 1) * 10;
      let end = start + 10;

      for (let i = start; i < end; i ++) {
        if (votes[i]) this.showVote.push(votes[i]);
      }
    },

    async getActInfo() {
      this.loadingActData = true;
      const { data: { success, data, msg } } = await this.$http.get(`/activity/${this.$route.params.id}`);
      // const { data: { success, data, msg } } = await this.$http.get(`https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getActById`);

      for (let i in data) {
        if (!data[i]) data[i] = '';
      }

      if (success) {

        this.actInfo = data;
        this.active = data.activityDO;

        this.rpTotal = data.redPackets.length || 0;
        this.voteTotal = data.votes.length || 0;
        this.signsTotal = data.signs.length || 0;
        this.subsTotal = data.subscrible.length || 0;
        this.lotsTotal = data.lotteries.length || 0;

        this.setRPPage(1);
        this.setVotePage(1);
        this.setSignsPage(1);
        this.setSubsPage(1);
        this.setLotsPage(1);
        this.loadingActData = false;

        return;
      }

      // this.$notify.error({
      //   title: '错误',
      //   message: msg
      // });
      this.loadingActData = false;
      
    },

    async getAllTags() {
      // const { data: { data, success, msg } } = await this.$http.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getAllTags');
      const { data: { success, data, msg } } = await this.$http.get('/activity-type');

      if (success) {
        this.tags = data;
      }
    }
  },

  mounted() {
    this.setNavActive('1');
    this.setCurBread(actdetail);

    this.getActInfo();
    this.getAllTags();
  }
}
</script>

<style lang="scss" scoped>
  @import '../../assets/scss/var.scss';
  .actinfo {
    font-size: 0;

    label {
      width: 90px;
      color: #99a9bf;
    }
    .el-form-item {
      margin-right: 0;
      margin-bottom: 0;
      width: 50%;
    }
  }

  #act-detail {

    

    .actType {
      span {
        cursor: pointer;
      }

      .active {
        color: $base-color;
      }
    }
  }
</style>



