<template>
  <section id="actmgr">

    <div class="empty"></div>

    <el-tabs tab-position="left">
      <el-tab-pane label="活动管理">

        <el-card class="box-card" body-style="padding: 0;">
          <div slot="header" class="clearfix">
            <span>活动管理</span>
          </div>
          
          <div>
            <el-table
              :data="actList"
              v-loading="loading"
              border
              style="width: 100%; border: 0 none;">
              <el-table-column
                prop="name"
                label="活动名">
              </el-table-column>
              <el-table-column
                prop="typeName"
                label="类型">
              </el-table-column>
              <el-table-column
                prop="max"
                label="人数">
              </el-table-column>
              <el-table-column
                prop="area"
                label="地区">
              </el-table-column>
              <el-table-column
                label="操作"
                >
                <template slot-scope="scope">
                  <el-button type="primary" size="mini" @click="detail(scope.row.id)">详情</el-button>
                  <el-button type="danger" size="mini" @click="delact(scope.row.id)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>

            <div class="block" style="text-align: center;">
              <el-pagination
                @current-change="setpage"
                :current-page="1"
                :page-size="20"
                layout="total, prev, pager, next, jumper"
                :total="total">
              </el-pagination>
            </div>

          </div>
        </el-card>

      </el-tab-pane>

      <el-tab-pane label="标签管理">

        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>标签管理</span>
          </div>
          
          <div v-loading="tagLoading">

            <el-row>
              <el-col :span="20">
                <div class="grid-content">
                  <el-input v-model="addTagData" placeholder="新标签名"></el-input>
                </div>
              </el-col>
              <el-col :span="4">
                <div class="grid-content">
                  <el-button type="primary" style="width: 100%;" @click="addTag">添加</el-button>
                </div>
              </el-col>
            </el-row>

            <div class="empty"></div>

            <div>
              <el-tag
                v-for="item in tags"
                :key="item.id"
                @close="delTag(item)"
                closable>
                <span style="cursotr: pointer" @click="updateTagName(item)">{{ item.name }}</span>
              </el-tag>
            </div>

            <el-dialog
              title="修改标签"
              :visible.sync="showUpdateTagForm"
              width="30%">
              <el-input placeholder="新标签名" v-model="curUpdateTag.name"></el-input>
              <span slot="footer" class="dialog-footer">
                <el-button @click="showUpdateTagForm = false">取 消</el-button>
                <el-button type="primary" @click="updateTag">确 定</el-button>
              </span>
            </el-dialog>

          </div>
        </el-card>


      </el-tab-pane>
    </el-tabs>

    <div class="empty"></div>

  </section>
</template>

<script>
import { mapMutations } from 'vuex';
import { actmgr } from '../../common/config/bread';
import post from '../../assets/js/utils';

export default {
  data() {
    return {
      addData: {
        tagName: ''
      },
      showAddForm: false,

      total: 0,

      actList: [],
      
      loading: false,

      tags: [],
      addTagData: '',
      tagLoading: false,
      showUpdateTagForm: false,
      curUpdateTag: {
        id: 0,
        name: ''
      }
    };
  },

  methods: {
    ...mapMutations({
      setNavActive: 'setNavActive',
      setCurBread: 'setCurBread',
    }),

    async delact(id) {
      const { success, data, msg } = await post(`/activity/${id}/del`);

      if (success) {
        this.$notify({
          title: '成功',
          message: msg,
          type: 'success'
        });

        this.actList.forEach((item, index) => {
          if (item.id === id) {
            this.actList.splice(index, 1);
          }
        });
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    },

    setpage(index) {
      this.getAllAct(index);
    },

    detail(id) {
      this.$router.push(`/backstage/actmgr/${id}`);
    },

    async getAllAct(pagenum = 1) {
      this.loading = true;
      // const { data: { success, data, msg } } = await this.$http.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getAllAct', {
      const { data: { success, data, msg } } = await this.$http.get('/activity/type/0', {
        params: {
          pagenum,
          pagesize:20
        }
      });

      if (success) {
        this.actList = data.rows;
        this.loading = false;
        this.total = data.total;
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });

      this.loading = false;
    },

    async getAllTags() {

      this.tagLoading = true;

      // const { data: { success, data, msg } } = await this.$http.get('https://www.easy-mock.com/mock/5ac23aa543c0ae62e03142b2/kang/getAllTags');
      const { data: { success, data, msg } } = await this.$http.get('/activity-type');

      if (success) {
        this.tags = data;
        this.tagLoading = false;
        return;
      }

      // this.$notify.error({
      //   title: '错误',
      //   message: msg
      // });
      this.tagLoading = false;
    },

    async delTag(item) {

      const {success, data, msg} = await post(`/activity-type/${item.id}/del`);

      if (success) {

        this.tags.forEach((itemer, index) => {
          if (itemer.id === item.id) {
            this.tags.splice(index, 1);
          }
        });

        return;
      }
      
      this.$notify.error({
        title: '错误',
        message: msg
      });
      
    },

    async addTag() {

      this.tags.push({
        name: this.addTagData
      });

      const { success, data, msg } = await post('/activity-type', {
        name: this.addTagData
      });

      console.log(data);

      if (success) {
        this.getAllTags();
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    },

    async updateTagName(item) {
      this.curUpdateTag = {...item};
      this.showUpdateTagForm = true;
    },

    async updateTag() {
      const { success, data, msg } = await post('/activity-type/put', this.curUpdateTag);

      if (success) {
        this.$notify({
          title: '成功',
          message: msg,
          type: 'success'
        });
        return;
      }

      this.$notify.error({
        title: '错误',
        message: msg
      });
    }
  },

  mounted() {
    this.setNavActive('1');
    this.setCurBread(actmgr);

    this.getAllAct();
    this.getAllTags();
  }
}
</script>

<style lang="scss" scoped>
  .el-tag {
    margin: 0 5px 5px 0;
  }
</style>



