<template>
  <div id="backstage">
    <nav-header></nav-header>

    <div class="body">
      <div class="nav">
        <Naver />
      </div>
      <div class="container" :style="{ height: bodyHeight + 'px' }">
        <el-card class="box-card">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item v-for="(item, index) in curBread" :key="index" :to="{'path': item.path}">{{ item.text }}</el-breadcrumb-item>

          </el-breadcrumb>
        </el-card>
        
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import NavHeader from '@/components/header/';
import Naver from '@/components/nav/';
import { mapGetters } from "vuex";
import post from '../../assets/js/utils';

export default {
  data() {
    return {
      bodyHeight: document.documentElement.clientHeight - 60
    };
  },
  computed: {
    ...mapGetters(["curBread"])
  },
  components: {
    NavHeader,
    Naver
  },
  mounted() {
    window.addEventListener('resize', () => {
      this.bodyHeight = document.documentElement.clientHeight - 60;
    });
  }
};
</script>

<style lang="scss" scoped>
  .body {
    width: 1300px;
    display: flex;
    margin: 0 auto;

    .nav {
      width: 220px;
      overflow: hidden;
      overflow-y: scroll;
      padding: 10px 0;
    }

    .container {
      flex: 1;
      overflow: hidden;
      overflow-y: scroll;
      padding: 10px;
      box-sizing: border-box;
    }
  }
</style>



